body {
    margin: 0;
    padding: 0;
}

.login-container {
    width: 100%;
    height: 100vh;
    background-color: rgb(80, 80, 161);
    display: flex;
    justify-content: center;
    align-items: center;
    background-position: center;
    background-repeat: cover;
    background-attachment: fixed;
    background-image: url('./../img/studio-background-concept-abstract-empty-light-gradient-purple-studio-room-background-product.jpg');
}

#formulario {
    width: 500px;
    height: 400px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
}
#formulario input[type='text'],
.MuiTextField-root {
    margin-bottom: 10px !important;
    width: 300px;
}

#formulario button {
    margin-top: 10px;
    margin-right: 10px;
}

#formulario .error {
    color: red;
    padding: 8px;
    background-color: rgb(253, 247, 188);
    border-radius: 5px;
}
.menu {
    width: 100%;
    background-color: rgb(17, 121, 86);
    padding: 20px;
    display: flex;
    justify-content: space-around;
    color: white;
}
.menu h2 {
    margin-top: 0px;
    margin-bottom: 0px;
    color: white;
}

.todo-content {
    width: 100%;
    background-color: #eee;
    height: max-content;
    padding: 20px;
}
.todo-list {
    display: flex;
    flex-wrap: wrap;
}
.todo,
.MuiPaper-root {
    padding: 25px;
    background: white;
    margin: 10px;
    border-radius: 5px;
    box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
}
.pendiente {
    border-color: rgb(94, 159, 245);
    box-shadow: 0px 0px 3px 0px rgba(94, 159, 245, 0.75);
    -webkit-box-shadow: 0px 0px 3px 0px rgba(94, 159, 245, 0.75);
    -moz-box-shadow: 0px 0px 3px 0px rgba(94, 159, 245, 0.75);
}

.pendiente h3 {
    color: rgb(94, 159, 245);
}
.caducado {
    border-color: rgb(248, 65, 65);
}
.caducado h3 {
    color: rgb(248, 65, 65);
}
.terminado {
    border-color: green;
}
.terminado h3 {
    color: green;
}
